@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:"Roboto", sans-serif}label{font-size:1.4rem}.Details .row{position:relative}.Details .row img{border-radius:.25rem}.Details .closeButton{position:absolute;top:0;left:15px}.Details .closeButton:hover{opacity:.6;text-decoration:none}.Drinks a{text-decoration:none;color:currentColor}.Drinks .card:hover{opacity:.6}

